body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", "Titillium Web",
    -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", cursive, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.smaller {
  font-size: smaller;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.leaflet-popup-content {
  word-break: break-word;
  white-space: pre-line;
}